.view-more__arrow {
  cursor: pointer;
  text-align: center;
}

.view-more__arrow a {
  display: inline-block;
  font-family: "Gotu", sans-serif;
  font-weight: 400;
  font-size: 0.875rem;
  position: relative;
  padding: 1rem 5.5rem 1rem 2.5rem;
  background-color: #408F95;
  color: #fff;
  overflow: hidden;
  z-index: 1;
  transition: 0.3s cubic-bezier(0.45, 0, 0.55, 1);
  box-shadow: 4px 4px 0 0 #fff, 4px 4px 0 1px #408F95;
}

.view-more__arrow a::first-letter {
  text-transform: uppercase;
}

.view-more__arrow a::before,
.view-more__arrow a::after {
  content: "";
  position: absolute;
}

.view-more__arrow a::before {
  width: 100%;
  height: 100%;
  border: 1px solid #408F95;
  top: 0;
  left: 0;
}

.view-more__arrow a::after {
  background-color: #fff;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -2;
}

.view-more__arrow a:hover {
  color: #408F95;
}

.view-more__arrow a:hover:after {
  transform: scale(1, 1);
}

.view-more__arrow a:hover span,
.view-more__arrow a:hover span::after {
  background-color: #408F95;
}

.view-more__arrow span,
.view-more__arrow span::after {
  content: "";
  position: absolute;
  display: block;
  background-color: #fff;
}

.view-more__arrow span {
  width: 2.5rem;
  height: 0.0625rem;
  right: 2.25rem;
  top: 50%;
  transform: translateY(-50%);
}

.view-more__arrow span::after {
  width: 0.375rem;
  height: 0.0625rem;
  right: 0;
  bottom: 0.125rem;
  transform: rotate(35deg);
}

